<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Piechrt text-center">
    <div
      v-if="PieWidgetData.length === 0 && isLoading === false"
      class="text-danger no-record"
    >
      {{ $t("NoRecordfound") }}
    </div>
    <div v-else :id="'pieDiv_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index">
      <loader :isVisible="isLoading"></loader>
      <div
        v-bind:class="{
          'd-inline-block mt-2 reportBlock t-min-height align-center c3 w-100':
            PieWidgetData.length > 0,
          'd-inline-block mt-2 align-center c3 w-100':
            PieWidgetData.length == 0,
        }"
        :controller="moduleCode"
        :id="'div_PieChart_' + action.replace(/\s+/g, '_') + index"
        v-bind:style="{
          'width:60%; float:left; text-align:center;': PieWidgetData.length > 0,
          'width:100%;text-align:center;': PieWidgetData.length == 0,
        }"
      >
      </div>
    </div>
  </div>
</template>
<script>
import "d3";
import "c3";
import DataService from "../../../services/DataService";
export default {
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    reportId: null,
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "pie",
      PieWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      chart: null, // Add a chart property to store the C3.js chart instance
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
  },

  created: function () {
    this.FetchData();
  },
  methods: {
    FetchData: function () {
      var vueObj = this;
      vueObj.isLoading = true;
      // var params = `moduleName=${vueObj.moduleName}&reportName=Lead_By_Industry&fromDate=2023-03-20&toDate=2023-03-26&usersDash=`;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=`;
      DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            // if (vueObj.widgetInfo.charT_TYPE == "table" && response.data.result != null) {
            //     vueObj.HeaderData = response.data.result.headers;
            // }
            if (response.data.result != null) {
              vueObj.PieWidgetData = response.data.result.data;
              vueObj.bindWidget(vueObj);
            }
          }
        }
        vueObj.isLoading = false;
      });
    },
    bindWidget: function (vueObj) {
      if (vueObj.charT_TYPE && vueObj.PieWidgetData.length > 0) {
        var xCategory = "";
        var yCategory = "";
        var arrAUP = [];
        // eslint-disable-next-line no-undef
        $.each(vueObj.PieWidgetData, function (index, element) {
          arrAUP.push([
            element.NAME + " (" + element.VALUE + ")",
            element.VALUE,
          ]);
        });
        var selector = "#div_PieChart_" + vueObj.action.replace(/\s+/g, '_') + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        let c3Obj = {
          bindto: selector,
          data: {
            columns: arrAUP,
            type: "pie",
            empty: {
              label: {
                position: "right",
                text: vueObj.$t("NoRecordfound"),
              },
            },
          },
          tooltip: {
            grouped: false
          },
          pie: {
            label: {
              format: function (value) {
                // eslint-disable-next-line no-undef
                return d3.format("")(value);
              },
            },
          },
          //   size: {
          //     width: chartWidth,
          //     height: chartHeight,
          //   },
          legend: {
            show: true,
            position: "bottom",
            height: '200px',
          },
          color: {
            pattern: [
              "#23A4FF",
              "#FF6B6B",
              "#5FB49C",
              "#776274",
              "#916953",
              "#28587B",
              "#F4B701",
              "#272635",
              "#AEE5D8",
              "#FFC482",
              "#3C6E71",
              "#9FA0FF",
              "#D1B1CB",
              "#95B2B8",
              "#EF8354",
              "#72D9C8",
              "#C6EBBE",
              "#56203D",
              "#A09CB0",
              "#5C573E",
            ],
          },
          axis: {
            rotated: false,
            x: {
              label: {
                text: "",
                position: "outer-center",
              },
              categories: [xCategory],
              type: "category",
            },
            y: {
              label: {
                text: yCategory,
                position: "outer-center",
              },
            },
          },
        };
        // eslint-disable-next-line no-undef
        var Piechrt = c3.generate(c3Obj);
        // eslint-disable-next-line no-undef
        $(selector).data("c3-chart", Piechrt);
      }
    }
  },
};
</script>
<style src="c3/c3.css"></style>
